import counter from './counter.vue';
export default {
	props:{
		spaceInfo: Object,
		goodsInfo:Object,
		goodsData:Object
	},
	methods:{
		//隐藏模态框
		hideSpecifications(){
			// this.spaceInfo.showSpace=false;
			this.$emit("hideSpec")
		},
		//模态框选择规格
		handleSelectSpecification(item){
			//组件里不能直接改动父级的数据
			// this.goodsInfo.spec=item;
			
			// 需要通过子级注册事件 父级执行方法 
			this.$emit("setSelectSpec",item)
		},
		sub(){
			this.$emit("sub")
		},
		add(){
			this.$emit("add")
		}
	},
	components:{
		counter
	}
}
